import {PerfGraphThreshold} from "@generated/perf-graph-threshold";

/**
 * The definition of a perf chart
 */
export class PerfGraph {
   /**
    * id of the graph, used for the 'ask vmware' link
    */
   id: string;

   /**
    * The display name of a perf graph
    */
   label: string;

   /**
    * chart data type
    */
   unit: string;

   description: string;

   /**
    * The line definitions of a chart
    */
   metrics: PerfGraphMetrics[];

   threshold: PerfGraphThreshold;

   /**
    * In performance chart view, all the charts are pivotal by default.
    * But several charts in the Performance for support view are not pivotal,
    * they should be displayed only when user decides to see the detailed data.
    */
   isPivotalGraph: boolean = true;

   get isHistogram(): boolean {
      return this.id.endsWith("histogram");
   }

   secondGraph: PerfGraph;

   static createPerfGraph = (id: string,
         label: string,
         unit: string,
         metrics: PerfGraphMetrics[],
         threshold?: PerfGraphThreshold,
         isPivotal: boolean = true): PerfGraph => {
      const graph: PerfGraph = new PerfGraph();
      graph.id = id;
      graph.label = label;
      graph.metrics = metrics;
      graph.threshold = threshold;
      graph.unit = unit;
      graph.isPivotalGraph = isPivotal;
      return graph;
   };
}

/**
 * Defines a single line in a perf chart, usually a perf chart contains several lines
 */
export class PerfGraphMetrics {
   /**
    *
    * @param key: The key of the line, we will use this key to find the actual chart data
    * @param label: display name of the chart line, using for chart legend
    * @param group: The group label of the performance metric.
    * @param description: line description, currently not used.
    * @param aggregatedBy: The value of this metrics is aggregated by the specified metrics values
    * @param unit: Specified unit for the metric. Some graphs can have metrics with different unit types.
    */
   constructor(public key: string, public label: string, public group?: string,
         public description?: string, public aggregatedBy?: string[], public unit?: string) {}
}