import {ManagedObject} from "@service/managed-object";

/**
 * This object is used in the most of the single entity perf pages, and some of the multi-entities
 * pages need to extend this object to display the metrics and the charts
 */
export class BasePerfEntity {
   configSpec: any; /* VsanPerfEntityType */
   additionalConfigSpecId: any;
   entityName: string;
   /**
    * the entity name that will be shown in the UI.
    */
   entityLabelName: string;
   /**
    * the entityUuid is the uuid of the queried object. It is also used for preselection in perf-multi-entities-metrics
    * component.
    */
   entityUuid: string;

   /**
    * the entityId is used to get the configSpec from the entity types object
    * one example "com.vmware.vsan.perf.entity.cluster-domclient"
    */
   entityId: string;

   /*
   * MoRef of the entity
   * TODO: mavrodievd Replace these + entityName with InventoryNode
   * */
   specifiedObjectRef: ManagedObject;

   icon: string;

   /**
    * This is the keyword for backend side to query the data. It's defined in the configSpec.
    */
   get entityType(): string {
      return this.configSpec ? this.configSpec.name : null;
   }

   /**
    * This method is used to get the key for ConsolidatedViewHelper.entityVisibilityStateMap
    * and PerfOrchestratorService.entitySelectionStateMap. We need this because now
    * PerfOrchestratorService is also consumed by File share performance. In this view the
    * specifiedObjectRef is not set.
    */
   get entityIdentifier(): string {
      if (this.specifiedObjectRef) {
         return this.specifiedObjectRef.value;
      }
      return this.entityUuid;
   }

   equals(that: BasePerfEntity): boolean {
      if (!that) {
         return false;
      }
      return this.entityId === that.entityId
            && this.entityUuid === that.entityUuid
            && this.configSpec === that.configSpec;
   }
}
